.banner {
  background-image: url("../../../images/HeroImage (2).png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  height: 60vh;
  /* display: flex;
  align-items: center;
  justify-content: center; */
}

.container {
  position: relative;
  height: 100%;
}

.card {
  /* background-color: rgb(129, 129, 129); */
  width: 70%;
  height: max-content;
  padding-inline: 50px;
  position: absolute;
  margin: auto 0;
  top: 0;
  bottom: 0;
}

.card h1 {
  line-height: 70px;
  color: #324277;
}

.joinButton {
  margin-block: 30px;
  color: white;
  font-size: x-large;
  padding-inline: 50px;
  padding-block: 15px;
  border-radius: 50px;
}

