.container {
    padding: 50px;
    background-image: url("../../../images/joiningWaitingListBg.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    position: relative;
    height: 100%;
} 

.form {
    /* position: absolute; */
    width: 30%;
    background-color: #FFFFFF;
    border-radius: 20px;
    height: max-content;
    margin: auto;
    /* top: 0;
    bottom: 0;
    left: 0;
    right: 0; */
    padding: 40px 10px;
    margin-top: 20px;
}

.formControl {
    width: 90%;
    margin: auto;
}


.formControl > input {
    width: 100%;
    color: #8D96B3;
    background-color: #E8EAEF;
    margin: 10px 0;
    border-radius: 10px;
    height: 40px;
    font-size: 20px;
    padding: 10px;
}

.formControl > input:focus {
    outline: none;
}

.container img {
    width: 80px;
    margin: auto;
}

.container h3 {
    color: white;
    font-family: Gilroy-SemiBold;
    font-size: 30px;
    width: 60%;
    margin: auto;
    text-align: center;
}

.textarea {
    width: 100%;
    resize: none;
    color: #8D96B3;
    background-color: #E8EAEF;
    margin: 10px 0;
    border-radius: 10px;
    height: 80px;
    font-size: 20px;
    padding: 10px;
}

.textarea:focus {
    outline: none;
}

.submitBtn {
    background-color: #1EAA4C;
    color: white;
    border-radius: 30px;
    width: max-content;
    padding: 10px 30px;
    font-size: 20px;
    
}

.popup {
    background-color: black;
}

.modalPara {
    font-size: 20px;
}