.contactUsSec {
  background-image: url("../../../images/contactBg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  height: auto;
  overflow: auto;
}

.contactUs {
  height: auto;
}

.contactUs > div {
  width: 50%;
}

.form {
  border-radius: 10px;
  padding: 20px 40px;
  width: 100%;
}

.input {
  width: 100%;
  background-color: #e8eaef;
  margin: 3vh 0;
  border-radius: 10px;
}

.formInput {
  background-color: #e8eaef;
  width: 100%;
  border: none;
  outline: none;
  color: #8d96b3;
  font-family: Gilroy-SemiBold;
  font-size: 22px;
  border-radius: 10px;
  padding: 6px 10px;
}

.formInput:focus {
  border: none;
  outline: none;
}

.textarea {
  background-color: #e8eaef;
  width: 100%;
  border: none;
  outline: none;
  color: #8d96b3;
  font-family: Gilroy-SemiBold;
  font-size: 22px;
  border-radius: 10px;
  padding: 6px 10px;
  resize: none;
}

.btnContainer {
  width: 100%;
  margin: auto;
  text-align: center;
  margin-top: 4vh;
}

.submitBtn {
  background-color: #1eaa4c;
  color: #ffffff;
  font-family: Gilroy-SemiBold;
  font-size: 22px;
  border-radius: 40px;
  padding: 10px 40px;
}

@media only screen and (max-width: 640px) {
  .contactUsSec {
    background-size: cover;
    height: 80vh;
  }

  .contactUs {
    flex-direction: column;
  }

  .contactUs > div {
    width: 80%;
    margin: 15px auto;
  }
}
