.aboutContainer > div {
  width: 47%;
}

.aboutContainer > div h1 {
  color: #324277;
  font-size: 60px;
  font-family: Gilroy-Bold;
}

.aboutContainer > div h3 {
  color: #747474;
  font-size: 25px;
  font-family: Gilroy-Bold;
}

.aboutContainer > div p {
  color: #868686;
  font-size: 15px;
  font-family: Gilroy-SemiBold;
}

@media only screen and (max-width: 640px) {
  .aboutContainer {
    flex-direction: column;
  }

  .aboutContainer > div {
    width: 80%;
    text-align: center;
  }

  .aboutContainer > div h1 {
    word-break: break-all;
  }
}
