.header {
  padding: 2vh 2.2vw;
  border-radius: 3vw;
}

.header h2 {
  margin-top: 0.3vw;
  color: #262626;
  font-size: 1.2vw;
  font-family: Gilroy-Bold;
}

.header > div > div {
  margin: auto;
  padding: 0 1.5vw;
  border-right: 1px solid #dddddd;
  display: flex;
  height: 100%;
}

.header > div button {
  background: transparent linear-gradient(90deg, #0587d4 0%, #00f260 100%) 0% 0%
    no-repeat padding-box;
  box-shadow: 10px 20px 30px #2164f433;
  border-radius: 3vw;
  opacity: 1;
  padding: 1vh 1vw;
  font-family: Gilroy-SemiBold;
  font-size: 1vw;
  color: #ffffff;
  display: flex;
  align-items: center;
}

.header > div button img {
  width: 1.3vw;
  margin-right: 0.5vw;
}

.notification {
  position: relative;
}

.notification div {
  right: 1.2vw;
  top: -0.2vw;
  position: absolute;
  width: max-content;
  height: max-content !important;
  padding: 0.2vw 0.6vw !important;
  font-size: 0.6vw;
  border-radius: 0.4vw;
  background-color: #e23428;
  color: #ffffff;
}

.notification svg {
  font-size: 2vw;
  color: #0587d4;
  margin: auto;
}

/* .user {
    width: 6vw;
    height: 6vh;
    border-radius: 50%;
} */

.user {
  flex-direction: row-reverse;
}

.user > div p {
  color: #000000 !important;
  font-family: Gilroy-Bold;
  font-size: 1.2vw !important;
  margin-right: 1vw;
}

.user img {
  width: 2.5vw;
  height: 2.5vw;
  border-radius: 50%;
  margin: auto;
  border: solid 0.22vw #0587d4;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title svg {
  color: #0587d4;
  font-size: 1.2vw;
  margin-right: 1vw;
  margin-top: 0.3vw;
}

.title svg:hover {
  cursor: pointer;
}

/* ------------------------------------  MEDIA QUERY  ---------------------------------------------- */

@media only screen and (max-width: 640px) {
  .header h2 {
    font-size: 3.8vw;
  }

  .title svg {
    font-size: 4vw;
  }

  .menuBox button {
    font-size: 2.5vw !important;
    letter-spacing: 0.4vw;
  }

  .menuBox button img {
    width: 3.5vw !important;
  }

  .notification svg {
    font-size: 5vw;
  }

  .notification div {
    font-size: 2vw;
  }

  .user img {
    width: 5vw;
    height: 5vw;
  }

  .user > div p {
    display: none;
  }
}
