.ourmission {
  padding: 70px 0;
}

.container {
  display: flex;
  gap: 70px;
  max-width: 1150px;
  /* background-color: #324277; */
}

.card {
  padding: 30px 30px;
  border-radius: 45px;
  width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.backgroundImage {
  background-image: url("../../../images/creature-card-background.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.card h1 {
  font-family: Gilroy-Medium;
  color: white;
  font-size: 30px;
  font-weight: 700;
  margin-top: 10px;
}

.card p {
  border-left: 0.4vw solid #324277;
  padding: 15px;
  font-size: 15px;
  font-family: Gilroy-SemiBold;
  color: #1f1f1f;
  text-align: left;
}
.card h2 {
  color: #324277;
  font-family: Fredoka-Bold;
  font-size: 20px;
}

.creature {
  width: 120px;
  margin: 0 auto;
  margin-bottom: 2vh;
}

@media only screen and (max-width: 640px) {
  .container {
    flex-direction: column;
    align-items: center;
  }
  .card {
    width: 80%;
  }
}
